import React, { useState } from 'react'
import { Link } from "react-router-dom";
import image from './images/sign-up.png'
import './log-in-sign-up/log-in-sign-up.css'
import Loader from './images/loader.png'
import Cookie from 'js-cookie';

const apiUrl = process.env.REACT_APP_API_URL_PRODUCTION;


export default function SignUp () {

  const [isLoading, setIsLoading] = useState(false)
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [isMatch, setIsMatch] = useState(true)

  const isFormValid = () => {
    return isMatch && email && username && password && confirmPassword;
  };

  
const handleClick = async () => {
  setIsLoading(true);

  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      username: username,
      email: email,
      password: password,
      confirm_password: confirmPassword,
      first_name: firstName,
      last_name: lastName
    })
  };

  const response = await fetch(apiUrl+'/user/register/', requestOptions);
  const data = await response.json();

  if (response.status === 201 && data) {
    // Save tokens in cookies
    Cookie.set('access_token', data.access_token);
    Cookie.set('refresh_token', data.refresh_token);
    Cookie.set('verification_link', data.verification_link);

    console.log(data);
    window.location.href = '/feed';
  } else {
    // Show error
    console.log("Sign-up failed");
  }

  setIsLoading(false);
};

  console.log('log-in')
  return (
    <div className='log-in-sign-up'>
      <img className='login-image' src={image} alt='LogInImage' />
      <div className='sign-up-container-box'>
        <div className='log-in'>
          <span>
            <span className='log-in-span'>Sign </span>
            <span className='log-in-span2'>Up</span>
          </span>
        </div>
        <div className='log-in-subtext'>
          One step closer to Exhibit your work.
        </div>
        <div className='sigup-container'>
        <div className='first-name-text-field'>
            <input 
              className='first-name' 
              placeholder='First Name' 
              value={firstName}
              onChange={e => setFirstName(e.target.value)}
              required>
              </input>
          </div>
          <div className='last-name-text-field'>
            <input 
              className='last-name' 
              placeholder='Last Name' 
              value={lastName}
              onChange={e => setLastName(e.target.value)}
              required></input>
          </div>
          <div className='email-text-field'>
            <input 
              className='email' 
              placeholder='Email' 
              value={email}
              onChange={e => setEmail(e.target.value)}
              required></input>
          </div>
          <div className='username-text-field'>
            <input 
              className='username' 
              placeholder='Username' 
              value={username}
              onChange={e => setUsername(e.target.value)}
              required></input>
          </div>
          <>
            <div className='password-text-field'>
              <input
                className={`password ${!isMatch ? 'red-border' : ''}`}
                placeholder='Password'
                required
                type='password'
                value={password}
                onChange={e => {
                  setPassword(e.target.value)
                  setIsMatch(e.target.value === confirmPassword)
                }}
              />
            </div>
            <div className='confirm-password-text-field'>
              <input
                className={`confirm-password ${!isMatch ? 'red-border' : ''}`}
                placeholder='Confirm Password'
                required
                type='password'
                value={confirmPassword}
                onChange={e => {
                  setConfirmPassword(e.target.value)
                  setIsMatch(e.target.value === password)
                }}
              />
            </div>
            {!isMatch && <div className="password-do-not-match-text">Passwords do not match</div>}
          </>
          
          <div 
                  className={`sign-up-button${!isFormValid() ? '-disabled' : ''}`} 
                  onClick={isFormValid() ? handleClick : null}
                >
                  {isLoading ? (
                    <img src={Loader} className='loader' alt='loading' />
                  ) : (
                    <div className='login-text'>Sign Up</div>
                  )}
                </div>
        </div>
        <div className='have-an-account-container'>
          <span>
            <span className='already-have-an-account-text'>
              Already have an account?{' '}
            </span>
            <Link to="/log-in" className='click-here-text'>Log In</Link>
          </span>
        </div>
      </div>
    </div>
  )
}
