import React from 'react'; 
import { Link } from "react-router-dom";
import index_image_1 from '../images/index-image-1.png';
import index_image_2 from '../images/index-image-2.png';
import './homepage.css';


export default function HomePage() {
  console.log('HomePage');
  return (

      <div className="desktop-lm-colour-spread">
        <img className="image-1" src={index_image_1} alt="image_1" />
        <div className="canvas-is-never-your-limit">
          Exhibit Inspire <br />Augmented Reality Art
        </div>
        <div className="dare-to-be-different">Dare to be different</div>
        <div className="dare-to-be-different-subtext">
          <p>Let your creativity run wild on ExInArt. Share your artwork with a community that celebrates the bold and the unique. Join us now and inspire others with your one-of-a-kind creations!</p>
        </div>
        <Link to="/sign-up">
          <button className="exhibition-button">
            <div className="exhibition">Get Started</div>
          </button>
        </Link>
        <Link to="/feed">
          <div className="share-your-art-button">
            <div className="share-your-art">Share your art</div>
          </div>        
        </Link>
        <img className="image-2" src={index_image_2} alt="image_2"/>
      </div>
  );
}
