import React from 'react'; 
import './about-us.css';

import team_member_1 from '../images/rayan-choucair.jpg';
import team_member_2 from '../images/rodolph-biainau.jpg';
import team_member_3 from '../images/jumana-badr.jpg';
import LogoBigBanner from '../images/logo.png'



export default function AboutUS() {
    console.log('ABout Us');
    return (
        <>
      <div className="meet-the-team-container">
        <div className="meet-the-team-text">
          <p>Meet the Team</p>
        </div>
        <div className="meet-the-team-subtext">
          <p>ExInArt is a project created by a team of students from the American Univeristy of Science and Technology.</p>
          <p>We are a group of passionate MBA students who are excited to introduce you to ExInARt</p>
        </div>
        <div className="team-members">
          <div className="team-member-1">
            <img className="team-member-1-image" src={team_member_1} alt="team_member_1"/>
            <div className="team-member-1-name">Rayan Choucair</div>
            <div className="team-member-1-role">MBA in Project Management</div>
          </div>
          <div className="team-member-2">
            <img className="team-member-2-image" src={team_member_2} alt="team_member_2"/>
            <div className="team-member-2-name">Rodolph Biainau</div>
            <div className="team-member-2-role">MBA in Project Management</div>
          </div>
          <div className="team-member-3">
            <img className="team-member-3-image" src={team_member_3} alt="team_member_3"/>
            <div className="team-member-3-name">Jumana Badr</div>
            <div className="team-member-3-role">MBA in Design Management</div>
          </div>
        </div>
        </div>
        <div className="project-information-container">
            <div className="project-information-text">
            <div className='logo-big-div-banner'>
            <img className='logo-big-banner' src={LogoBigBanner} alt='ExInARt'></img>
          </div>
                <p className="project-subtitle-text">Exhibit Inspire Augmented Reality Art</p>
            </div>
            <div className="project-information-subtext">
                <p>ExInARt is a platform that aims to create a supportive and vibrant community for artists in Lebanon. The platform provides a space for artists to connect, collaborate, exhibit, and inspire others with their work. It also offers art collectors a dedicated space to discover new art and emerging artists. ExInARt addresses the challenges faced by artists in Lebanon susch as the high rise in rent for art spaces to exhibit their art work or the difficulties in selling their artwork through social media platforms by facilitating the sale and accessibility of their artwork. The platform aims to foster a thriving artistic network in the country by bridging the gap between artists and art enthusiasts. 
                </p>
                <p>ExInARt introduces new art forms utilizing the power of augmented reality technology, providing emerging artists with a unique opportunity to reach a broader audience, connect with potential buyers, and engagingly showcase their artwork. The platform empowers artists by creating opportunities enabling them to showcase their artwork, create virtual galleries, generate artistic portfolios, engage with viewers, exhibit their work by utilizing augmented reality, connect with the art community, get feedback from critics, and benefit from marketing and promotion plans. 
                </p>
                <p>ExInARt serves as a supportive platform for emerging artists who may lack guidance in their careers, providing them with valuable resources and opportunities. The platform facilitates connections between clients and artists, creating new doors for collaborations, commissions, and sales. By combining these elements, ExInARt aspires to contribute to the growth and development of the art community in Lebanon, promoting a vibrant and dynamic artistic network. Most importantly ExInARt will adopt emerging artist to thrive with their art in a broader frame and guide them through success and acknowledgment from artists and art enthusiasts abroad. As our goal for ExInARt is to show case artist on the platform to an international wider crowd.
                </p>
            </div>
        </div>
        </>

    );
  }
  