import React, { useEffect, useState, useCallback, useRef } from 'react';
import loader from '../images/loader.png'
import JSZip from 'jszip'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import * as THREE from 'three'
import './3d-display.css'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';


export default function Display3DObject ({ post }) {
    const [fileContent, setFileContent] = useState(null);
    const [loading, setLoading] = useState(false);
    const mount = useRef(null);
  
    const getFileType = url => {
      if (!url) return null;
      const path = url.split('?')[0];
      const type = path.split('.').pop();
      return type;
    };
  
    const fileType = getFileType(post?.image_or_fbx);

  const downloadAndExtractFile = useCallback(async (url, type) => {
    setLoading(true);

    try {
      const glbFileUrl = post.image_or_fbx;

      if (type === 'glb' || type === 'gltf') {
        renderGLB(glbFileUrl);
      } else if (type === 'zip') {
        const zip = new JSZip();
        const content = await zip.loadAsync(glbFileUrl);
  
        Object.keys(content.files).forEach(async (filename) => {
          const fileData = await content.files[filename].async('glbFileUrl');
          if (filename.endsWith('.glb') || filename.endsWith('.glbt')) {
            renderGLB(fileData); // Call renderGLB here as well, if you have GLB files within ZIP
          }
        });
      }
    } catch (error) {
      console.error(`Failed to download and unzip: ${error}`);
    }
  
    setLoading(false);
  }, [post.image_or_fbx]);
  
  

  const renderGLB = (glbUrl) => {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    scene.background = new THREE.Color(0x996600);

    camera.position.z = 6;

    const renderer = new THREE.WebGLRenderer();
    renderer.setSize(window.innerWidth, window.innerHeight);

    if (mount.current) {
      mount.current.appendChild(renderer.domElement);
    }
  
    // Lights
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambientLight);
    const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
    directionalLight.position.set(0, 1, 1);
    scene.add(directionalLight);
  
    // GLTF Loader
    const loader = new GLTFLoader();
    loader.load(
      glbUrl,
      (gltf) => {
        scene.add(gltf.scene);
      const box = new THREE.Box3().setFromObject(gltf.scene);
      const boxSize = box.getSize(new THREE.Vector3());
      const boxCenter = box.getCenter(new THREE.Vector3());
  
      // Adjust camera position and look at the center of the bounding box
      const maxSize = Math.max(boxSize.x, boxSize.y, boxSize.z);
      const fitHeightDistance = maxSize / (2 * Math.atan((Math.PI * camera.fov) / 360));
      const fitWidthDistance = fitHeightDistance / camera.aspect;
      const distance = Math.max(fitHeightDistance, fitWidthDistance);
  
      const direction = new THREE.Vector3()
        .subVectors(camera.position, boxCenter)
        .normalize()
        .multiplyScalar(distance);
  
      camera.position.copy(direction.add(boxCenter));
      camera.lookAt(boxCenter);
  
    },
    undefined,
    (error) => {
      console.error('An error happened', error);
    }
    );
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableZoom = true;
    controls.enablePan = true;
    controls.enableDamping = true;
    controls.minDistance = 0.1;
    controls.maxDistance = 0.5;
    controls.target.set(0, 0, 0);

    

    
  
    // Animation
    const animate = () => {
      requestAnimationFrame(animate);
        controls.update();
      renderer.render(scene, camera);
    };
  
    animate();
  };
  


  useEffect(() => {
    const fileType = getFileType(post?.image_or_fbx);

    if (fileType) {
      const normalizedFileType = fileType.toLowerCase();

      if (['glb', 'gltf', 'zip'].includes(normalizedFileType)) {
        downloadAndExtractFile(post.image_or_fbx, normalizedFileType);
      } else if (['jpeg', 'png', 'jfif', 'jpg'].includes(normalizedFileType)) {
        setFileContent(post.image_or_fbx);
      }
    }
  }, [post, downloadAndExtractFile, fileType]);
  


  return (
    <div className='content-section-img-fbx'>
      {loading && <img src={loader} alt='Loading...' />}
      {!loading && (fileType === 'jpg' || fileType === 'png' || fileType === 'jfif') ? (
        <img className='content-section-img' src={fileContent} alt='Post' />
      ) : (fileType === 'glb' || fileType === 'gltf' || fileType === 'zip') ? (
        <div className='content-section-3d' ref={mount}></div>  
      ) : null}
    </div>
  );
  
}
