import React, { useEffect, useRef } from 'react'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js'
import Scene1 from '../3d-scene/scene1.glb'
import './3d-scene-walker.css'

export default function Scene3D () {
  const mount = useRef(null)

  useEffect(() => {
    const manager = new THREE.LoadingManager()
    // const menuScreen = document.getElementById('menu-screen')

    // Setup
    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    )
    camera.position.z = 3
    camera.position.y = 1.5
    scene.background = new THREE.Color(0x996600)
    const currentMount = mount.current

    const renderer = new THREE.WebGLRenderer({ antialias: true })
    renderer.setSize(window.innerWidth, window.innerHeight)
    if (currentMount) {
      currentMount.appendChild(renderer.domElement)
    } else {
      console.error('mount.current is null')
    }

    // Initialize PointerLockControls
    const controls = new PointerLockControls(camera, document.body)
    scene.add(controls.getObject())

    // Add event listener for locking controls
    renderer.domElement.addEventListener('click', () => {
      controls.lock()
    })

    let moveForward = false
    let moveBackward = false
    let moveLeft = false
    let moveRight = false

    // Realistic Lighting
    const pointLight1 = new THREE.PointLight(0xffffff, 12, 1000)
    pointLight1.position.set(-3.4040724735112455, 2, -3.623290663412026)
    pointLight1.castShadow = true
    pointLight1.shadow.mapSize.width = 512
    pointLight1.shadow.mapSize.height = 512
    pointLight1.shadow.camera.near = 0.5
    pointLight1.shadow.camera.far = 500
    scene.add(pointLight1)

    const pointLight2 = new THREE.PointLight(0xffffff, 8, 100)
    pointLight2.position.set(-0.7843857702900924, 2, 4.979666795130763)
    pointLight2.castShadow = true
    pointLight2.shadow.mapSize.width = 512
    pointLight2.shadow.mapSize.height = 512
    pointLight2.shadow.camera.near = 0.5
    pointLight2.shadow.camera.far = 500
    scene.add(pointLight2)

    const pointLight3 = new THREE.PointLight(0xffffff, 7, 100)
    pointLight3.position.set(0.6672622092138245, 2, -5.254557013690256)
    pointLight3.castShadow = true
    pointLight3.shadow.mapSize.width = 512
    pointLight3.shadow.mapSize.height = 512
    pointLight3.shadow.camera.near = 0.5
    pointLight3.shadow.camera.far = 500
    scene.add(pointLight3)

    // Keydown event for moving lights
    const moveLightHandler = event => {
      switch (event.code) {
        case 'KeyM': // Move pointLight1 to camera's position
          pointLight1.position.set(
            controls.getObject().position.x,
            controls.getObject().position.y,
            controls.getObject().position.z
          )
          console.log(
            controls.getObject().position.x,
            controls.getObject().position.y,
            controls.getObject().position.z
          )
          break

        case 'KeyN': // Move pointLight2 to camera's position
          pointLight2.position.set(
            controls.getObject().position.x,
            controls.getObject().position.y,
            controls.getObject().position.z
          )
          console.log(
            controls.getObject().position.x,
            controls.getObject().position.y,
            controls.getObject().position.z
          )
          break

        default:
          break
      }
    }

    let isLocked = false // state variable to keep track of lock status

    // Initialize PointerLockControls
    controls.addEventListener('lock', function () {
      try {
        document.getElementById('menu-screen').style.display = 'none' // Hide menu
        isLocked = true
      } catch (e) {
        console.error('Error during lock:', e)
      }
    })

    controls.addEventListener('unlock', function () {
      try {
        document.getElementById('menu-screen').style.display = 'flex' // Show menu
        isLocked = false
      } catch (e) {
        console.error('Error during unlock:', e)
      }
    })

    // Your existing code to lock the controls
    renderer.domElement.addEventListener('click', () => {
      try {
        if (!isLocked) {
          setTimeout(() => {
            controls.lock()
          }, 1000) // delay locking by 100ms
        }
      } catch (e) {
        console.error('Error during click event:', e)
      }
    })

    // Add the event listener
    document.addEventListener('keydown', moveLightHandler)

    // Keyboard Controls
    const onKeyDown = event => {
      switch (event.code) {
        case 'KeyW':
          moveForward = true
          break
        case 'KeyA':
          moveLeft = true
          break
        case 'KeyS':
          moveBackward = true
          break
        case 'KeyD':
          moveRight = true
          break
        default:
          break
      }
    }

    const onKeyUp = event => {
      switch (event.code) {
        case 'KeyW':
          moveForward = false
          break
        case 'KeyA':
          moveLeft = false
          break
        case 'KeyS':
          moveBackward = false
          break
        case 'KeyD':
          moveRight = false
          break
        default:
          break
      }
    }

    document.addEventListener('keydown', onKeyDown)
    document.addEventListener('keyup', onKeyUp)

    // Load Scene1
    const loader = new GLTFLoader(manager) // pass the manager here
    loader.load(Scene1, gltf => {
      scene.add(gltf.scene)
    })

    const animate = () => {
      requestAnimationFrame(animate)

      if (controls.isLocked) {
        // Replace this with your actual movement logic
        if (moveForward) controls.moveForward(0.05)
        if (moveBackward) controls.moveForward(-0.05)
        if (moveLeft) controls.moveRight(-0.05)
        if (moveRight) controls.moveRight(0.05)
      }

      renderer.render(scene, camera)
      //   composer.render();
    }

    animate()

    // Cleanup
    return () => {
      document.removeEventListener('keydown', onKeyDown)
      document.removeEventListener('keyup', onKeyUp)
      document.removeEventListener('keydown', moveLightHandler)

      if (currentMount) {
        currentMount.innerHTML = '' // Or any other cleanup involving the mount
      }
    }
  }, [])

  return (
    <div className='scene-container'>
      <div className='scene-3d' ref={mount}></div>
      <div id='menu-screen' className='menu-screen'>
        <h1>Click on the Scene to Start</h1>
        <p>WASD to move</p>
        <p>M to move pointLight1</p>
        <p>N to move pointLight2</p>
      </div>
    </div>
  )
}
