import './feed.css'
import Cookie from 'js-cookie'
// import { apiFetch } from '../token-manager'
import React, { useEffect, useState } from 'react'
import Display3DObject from '../3d-renderer/3d-display.js'
import MyInterest from './my-interest.js'
import Hashtags from './hashtags.js'

const apiUrl = process.env.REACT_APP_API_URL_PRODUCTION

async function convertFBXtoGLB (file) {
  // Your logic to convert FBX to GLB
  return new File([file], 'converted.glb', { type: 'model/gltf-binary' })
}

export function CreatePost () {
  const [selectedFile, setSelectedFile] = useState(null)
  const [title, setTitle] = useState('')
  const [apiMessage, setApiMessage] = useState('')
  const [showFooter, setShowFooter] = useState(false)

  const handleFileChange = async e => {
    let file = e.target.files[0]
    if (!file) return

    if (file.name.endsWith('.fbx')) {
      file = await convertFBXtoGLB(file)
    }

    setSelectedFile(file)
  }
  const handleUpload = async () => {
    const formData = new FormData()
    formData.append('title', title)
    formData.append('image_or_fbx', selectedFile)

    const headers = {
      Authorization: `Bearer ${Cookie.get('access_token')}`,
      Accept: 'application/json'
    }

    try {
      const response = await fetch(`${apiUrl}/network/create-post/`, {
        method: 'POST',
        headers,
        body: formData
      })

      const data = await response.json()
      if (response.status === 201) {
        setApiMessage('Your post created successfully')
      } else {
        setApiMessage(data.message) // Assuming the message is in the 'message' field
      }
      setShowFooter(true)
      setTimeout(() => setShowFooter(false), 3000)
    } catch (error) {
      console.error('Fetch Error:', error)
    }
  }

  return (
    <div className='create-a-post'>
      <div className='rectangle-2'>
        <div className=''>
          <button className='send-button-box' onClick={handleUpload}>
            <svg
              className='send-icon'
              width='16'
              height='16'
              viewBox='0 0 16 16'
              fill='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <g clipPath='url(#clip0_5_9944)'>
                <path
                  d='M14.6668 1.33301L7.3335 8.66634'
                  stroke='white'
                  strokeLinecap='round'
                  strokeLinejoin='round'
                />
                <path
                  d='M14.6668 1.33301L10.0002 14.6663L7.3335 8.66634L1.3335 5.99967L14.6668 1.33301Z'
                  stroke='white'
                  strokeLinecap='round'
                  strokeLinejoin='round'
                />
              </g>
              <defs>
                <clipPath id='clip0_5_9944'>
                  <rect width='16' height='16' fill='white' />
                </clipPath>
              </defs>
            </svg>
          </button>
        </div>
        <button className='image-fbx-button'>
          <input
            className='select-file'
            type='file'
            onChange={handleFileChange}
            accept='.jpg,.png,.fbx,.glb'
          />
          <svg
            className='image-icon'
            width='24'
            height='24'
            viewBox='0 0 24 24'
            fill='none'
            xmlns='http://www.w3.org/2000/svg'
          >
            <g opacity='1'>
              <path
                d='M19 3H5C3.89543 3 3 3.89543 3 5V19C3 20.1046 3.89543 21 5 21H19C20.1046 21 21 20.1046 21 19V5C21 3.89543 20.1046 3 19 3Z'
                stroke='#808080'
                strokeWidth='2'
                strokeLinecap='round'
                strokeLinejoin='round'
              />
              <path
                d='M8.5 10C9.32843 10 10 9.32843 10 8.5C10 7.67157 9.32843 7 8.5 7C7.67157 7 7 7.67157 7 8.5C7 9.32843 7.67157 10 8.5 10Z'
                stroke='#808080'
                strokeWidth='2'
                strokeLinecap='round'
                strokeLinejoin='round'
              />
              <path
                d='M21 15L16 10L5 21'
                stroke='#808080'
                strokeWidth='2'
                strokeLinecap='round'
                strokeLinejoin='round'
              />
            </g>
          </svg>
        </button>
        <input
          value={title}
          onChange={e => setTitle(e.target.value)}
          className='what-s-on-your-mind'
          placeholder="What's on your mind?"
        ></input>
        <div className='line'></div>
        <div className='share-thoughts'>Share your Art</div>
      </div>
      <div className={`footer ${showFooter ? 'show' : ''}`}>{apiMessage}</div>
    </div>
  )
}

export default function DisplayPost ({ profile, title, image, glbFile }) {
  const [posts, setPosts] = useState([])

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const headers = {
          Accept: 'application/json',
          Authorization: 'Bearer ' + Cookie.get('access_token')
        }

        const response = await fetch(apiUrl + '/network/posts_list', {
          headers
        })

        if (response.ok) {
          const data = await response.json()
          setPosts(data.results)
        } else {
          console.error('Failed to fetch posts')
        }
      } catch (error) {
        console.error('An error occurred:', error)
      }
    }

    fetchPosts()
  }, [])

  // const handleComment = () => {
  //   // Your logic for commenting
  // };

  const handleLike = async postId => {
    const headers = {
      Authorization: 'Bearer ' + Cookie.get('access_token')
    }

    const response = await fetch(apiUrl + `/network/like/${postId}/`, {
      method: 'POST',
      headers
    })

    if (response.status === 200) {
      //TODO: update the like count
      // Successfully liked the post
      // Update your state or UI here
    } else {
      // Handle error
      const data = await response.json()
      console.error(data)
    }
  }

  const handleFollow = () => {
    // Your logic for following the user
  }

  return (
    <>
      <div className='create-a-post-function'>
        <CreatePost />
      </div>
      {posts.map(post => (
        <div className='post-container' key={post.id}>
          <div className='profile-section'>
            {/* Display the user's profile */}
            <img
              className='post-profile-pic'
              src={post.user.profile_picture}
              alt='User Profile'
            />
            <div className='user-info'>
              <h4 className='profile-username-text'>{post.user.username}</h4>
            </div>
          </div>
          <div className='title-section'>
            {/* Display the title of the post */}
            <h3>{post.title}</h3>
          </div>
          <Display3DObject post={{ image_or_fbx: post.image_or_fbx }} />
          <div className='action-section'>
            {/* Buttons for commenting, liking, and following */}
            {/*<button className="comment-button" onClick={() => handleComment(post.id)}>Comment</button>*/}
            <button className='like-button' onClick={() => handleLike(post.id)}>
              <svg
                className='thumbs-up'
                width='16'
                height='16'
                viewBox='0 0 16 16'
                fill='none'
                xmlns='http://www.w3.org/2000/svg'
              >
                <g clip-path='url(#clip0_5_9984)'>
                  <path
                    d='M4.66659 14.6663H2.66659C2.31296 14.6663 1.97382 14.5259 1.72378 14.2758C1.47373 14.0258 1.33325 13.6866 1.33325 13.333V8.66634C1.33325 8.31272 1.47373 7.97358 1.72378 7.72353C1.97382 7.47348 2.31296 7.33301 2.66659 7.33301H4.66659M9.33325 5.99967V3.33301C9.33325 2.80257 9.12254 2.29387 8.74747 1.91879C8.37239 1.54372 7.86369 1.33301 7.33325 1.33301L4.66659 7.33301V14.6663H12.1866C12.5081 14.67 12.8202 14.5573 13.0652 14.349C13.3102 14.1407 13.4717 13.8509 13.5199 13.533L14.4399 7.53301C14.4689 7.34191 14.456 7.1468 14.4021 6.96118C14.3483 6.77556 14.2547 6.60387 14.1278 6.45802C14.001 6.31216 13.844 6.19563 13.6676 6.11649C13.4913 6.03734 13.2999 5.99749 13.1066 5.99967H9.33325Z'
                    stroke='#0057FF'
                    strokeWidth='2'
                    strokeLinecap='round'
                    strokeLinejoin='round'
                  />
                </g>
                <defs>
                  <clipPath id='clip0_5_9984'>
                    <rect width='16' height='16' fill='white' />
                  </clipPath>
                </defs>
              </svg>
              <div className='like-count'>{post.likes_count}</div>
            </button>
            <button
              className='follow-button'
              onClick={() => handleFollow(post.user.id)}
            >
              Follow
            </button>
            <button className='comment-button'>
              <svg
                xmlns='http://www.w3.org/2000/svg'
                width='16'
                height='16'
                fill='currentColor'
                class='bi bi-chat-heart'
                viewBox='0 0 16 16'
              >
                <path
                  fill-rule='evenodd'
                  d='M2.965 12.695a1 1 0 0 0-.287-.801C1.618 10.83 1 9.468 1 8c0-3.192 3.004-6 7-6s7 2.808 7 6c0 3.193-3.004 6-7 6a8.06 8.06 0 0 1-2.088-.272 1 1 0 0 0-.711.074c-.387.196-1.24.57-2.634.893a10.97 10.97 0 0 0 .398-2Zm-.8 3.108.02-.004c1.83-.363 2.948-.842 3.468-1.105A9.06 9.06 0 0 0 8 15c4.418 0 8-3.134 8-7s-3.582-7-8-7-8 3.134-8 7c0 1.76.743 3.37 1.97 4.6a10.437 10.437 0 0 1-.524 2.318l-.003.011a10.722 10.722 0 0 1-.244.637c-.079.186.074.394.273.362a21.673 21.673 0 0 0 .693-.125ZM8 5.993c1.664-1.711 5.825 1.283 0 5.132-5.825-3.85-1.664-6.843 0-5.132Z'
                />
              </svg>
            </button>
          </div>
        </div>
      ))}
      <MyInterest />
      <Hashtags />
    </>
  )
}
