import React from 'react';
import { Outlet, Link, useNavigate } from 'react-router-dom';
import Cookie from 'js-cookie';
import './banner.css';
import LogoBanner from '../images/logo.png';

const apiUrl = process.env.REACT_APP_API_URL_PRODUCTION;

export default function Banner() {
  const navigate = useNavigate();
  const jwtToken = Cookie.get('access_token');
  const isLoggedIn = jwtToken !== undefined && jwtToken !== null;
  const firstName = Cookie.get('first_name');
  const lastName = Cookie.get('last_name');
  const profilePicture = Cookie.get('profile_picture');
  const verificationLink = Cookie.get('verification_link');
  const shouldShowVerificationLink = verificationLink && verificationLink !== 'undefined';

  const handleLogout = async () => {
    const accessToken = Cookie.get('access_token');
    const refreshToken = Cookie.get('refresh_token');
  
    // Using fetch directly
    const response = await fetch(`${apiUrl}/user/logout/`, {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${accessToken}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ refresh_token: refreshToken }),
      mode: 'cors', // If needed
    });
  
    if (response.ok) {
      Cookie.remove('access_token');
      Cookie.remove('refresh_token');
      navigate('/log-in');
    } else {
      console.error('Failed to log out');
    }
  };
  
  const openFigma = () => {
    window.open("https://www.figma.com/file/D3pP2uJ8UfJj4HbCtkdkbB/Rodolph's-website?node-id=104%3A88&mode=dev", "_blank");
  };


  return (
    <div className='top-banner'>
      <div className='rectangle-banner'></div>
      <Link to='/'>
        <div className='logo-div-banner'>
          <img className='logo-banner' src={LogoBanner} alt='ExInARt'></img>
        </div>
      </Link>
      <div className='menu-container-banner'>
        <div className='feed-banner'>
          <Link to='/feed'>Feed</Link>
        </div>
        <div className='ar-gallery-banner'>
          <Link to='/ar-gallery'>AR Gallery</Link>
        </div>
        <div className='about-us-banner'>
          <Link to='/about-us'>About Us</Link>
        </div>
      </div>
  
      {shouldShowVerificationLink && (
        <div className='verification-link-div'>
          <Link className='verification-link' to={verificationLink}>
            Verify Here
          </Link>
        </div>
      )}
        {isLoggedIn ? (
          <>
            <div className='profile-container'>
              <div className='profile-rectangle'>
                <div className='profile-image-square'>
                  <button  onClick={openFigma} className='profile-image'>
                    <img
                      className='profile'
                      src={profilePicture}
                      alt='Profile'
                    ></img>
                  </button>
                </div>
                <div className='name-text'>
                  {firstName} {lastName}
                </div>
                <div className='views-per-month'>367 views today</div>
                <div className='views-variance'>+32</div>
              </div>
            </div>
            <Link to='log-in'>
              <button
                className='log-out-rectangle-banner'
                onClick={handleLogout}
              >
                <div className='log-out-banner'>Log Out</div>
              </button>
            </Link>
          </>
        ) : (
          <>
            <Link className='sign-up-link-container' to='/sign-up'>
              <button className='sign-up-rectangle-banner'>
                <div className='sign-up-banner'>Sign Up</div>
              </button>
            </Link>
            <Link className='log-in-link-container' to='/log-in'>
              <button className='log-in-rectangle-banner'>
                <div className='log-in-banner'>Log In</div>
              </button>
            </Link>
          </>
        )}
      <Outlet />
    </div>
  )
}

