import React from 'react'
import index_image_1 from './images/ar-gallery-image-1.jpeg'
import index_image_2 from './images/ar-gallery-image-2.jpeg'
import './ar-gallery/ar-gallery.css'
import Walker3D from './3d-renderer/3d-scene-walker'
import SceneImage1 from './images/scene-images/scene-1.png'
import SceneImage2 from './images/scene-images/scene-2.png'
import SceneImage3 from './images/scene-images/scene-3.png'
import SceneImage4 from './images/scene-images/scene-4.png'
import SceneImage5 from './images/scene-images/scene-5.png'

export default function ARGallery () {
  console.log('HomePage')
  return (
    <div className='desktop-lm-colour-spread-ag'>
      <img className='image-1-ag' src={index_image_1} alt='image_1' />
      <div className='canvas-is-never-your-limit-ag'>
        Take a tour in our <br /> virtual gallery
      </div>
      <div className='canvas-is-never-your-limit-ag-subtext'>
        <p>
        Take a tour and prepare to be amazed. Each week, a new artist is showcased, given the opportunity to feature their live paintings and art in our virtual gallery experience. Wander through the exhibits and brace yourself for awe-inspiring moments. This platform provides artists with a unique opportunity to display their work to the world, challenging all boundaries and norms.
        </p>
      </div>
      <div className='dare-to-be-different-ag'>
      Bridging Classic Elegance with Digital Innovation.
      </div>
      <div className='dare-to-be-different-ag-subtext'>
        <p>
        Welcome to ExInArt, where timeless art and modern technology converge. Our AR gallery elevates classic pieces with digital innovation, creating an immersive, one-of-a-kind experience. Step into a space where the past and present are in perfect harmony.
        </p>
      </div>
      <img className='image-2-ag' src={index_image_2} alt='image_2' />
      <div className='walker-3d'>
      <div className='scene-walker-title'> Start your VR Tour</div>
        <Walker3D />
      </div>
      <div className='scene-selector-container'>
        <div className='scene-selector'>
          <div className='scene-selector-text'>Select your scene</div>
          <div className='scene-selector-image'>
            <img className='scene-image' src={SceneImage1} alt='Scene 1'></img>
            <img className='scene-image' src={SceneImage2} alt='Scene 2'></img>
            <img className='scene-image' src={SceneImage3} alt='Scene 3'></img>
            <img className='scene-image' src={SceneImage4} alt='Scene 4'></img>
            <img className='scene-image' src={SceneImage5} alt='Scene 5'></img>
          </div>
        </div>
      </div>
    </div>
  )
}
