import React, { useState } from 'react'
import { Link } from "react-router-dom";
import image from './images/log-in.png'
import './log-in-sign-up/log-in-sign-up.css'
import Loader from './images/loader.png'
import Cookie from 'js-cookie';
//jwt decoder



const apiUrl = process.env.REACT_APP_API_URL_PRODUCTION;

export default function LogIn () {
  const [isLoading, setIsLoading] = useState(false)
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')

  const handleClick = async () => {
    setIsLoading(true)

    const requestOptions = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ username: username, password: password })
    };

    const response = await fetch(apiUrl+'/user/login/', requestOptions);
    const data = await response.json();

    if (response.status === 200 && data) {
      Cookie.set('access_token', data.access_token);
      Cookie.set('refresh_token', data.refresh_token);
      Cookie.set('first_name', data.first_name);
      Cookie.set('last_name', data.last_name);
      Cookie.set('username', data.username);
      Cookie.set('user_id', data.user_id);
      Cookie.set('is_verified', data.is_verified);
      Cookie.set('profile_picture', data.profile_picture);
      Cookie.set('verification_link', data.verification_link)

      console.log('message: ', data.message);
      window.location.href = '/feed';
    } else {
      // Show error
      console.log("Login failed");
    }
    setIsLoading(false);

  }

  console.log('log-in')
  return (
    <div className='log-in-sign-up'>
      <img className='login-image' src={image} alt='LogInImage' />
      <div className='log-in-container-box'>
        <div className='log-in'>
          <span>
            <span className='log-in-span'>Log </span>
            <span className='log-in-span2'>in</span>
          </span>
        </div>
        <div className='log-in-subtext'>One step closer to Inspire.</div>
        <div className='username-password-container'>
          <div className='username-text-field'>
            <input
              className='username'
              placeholder='Username or Email'
              value={username}
              onChange={e => setUsername(e.target.value)}
              required
            ></input>
          </div>
          <div className='password-text-field'>
            <input 
            type='password'
            className='password' 
            placeholder='Password' 
            value={password}
            onChange={e => setPassword(e.target.value)}
            required></input>
          </div>
          <div className='login-button' onClick={handleClick} disabled={!username || !password }>
            {isLoading ? (
              <img src={Loader} className='loader' alt='loading' />
            ) : (
              <div className='login-text'>Login</div>
            )}
          </div>
        </div>
        <div className='have-an-account-container'>
          <span>
            <span className='already-have-an-account-text'>
              Don't have an account? &nbsp;
            </span>
            <Link className='click-here' to='/sign-up'>
            <span className='click-here-text'>Sign Up</span>
            </Link>
          </span>
        </div>
      </div>
    </div>
  )
}
